#foobar_notification-types_type-field {
  label {
	width: 120px;
	height: 120px;
	text-align: center;
	margin-left: 10px;
	line-height: 1.5;
	display: flex;
	justify-content: center;
	align-items: center;
	font-weight: bold;
	background: #eee;
	border-color: #ddd;

	&.foofields-selected {
	  border-color: #888;
	}

	&.foobar-pro {
	  .dashicons {
		color: #31be4d;
	  }
	}
  }
}

#foobar_notification-settings_color-field {
  span {
	display: inline-block;
	width: 32px;
	height: 32px;
  }
}

#foobar_notification-shortcode_shortcode-field {
  .foobar-shortcode {
	cursor: pointer;
	text-align: center;
	padding: 10px;
	font-family: "courier new", courier, serif;
	border: none;
	box-shadow: none;
	background: #efefef;
  }
}

.wp-list-table {
  .foobar-shortcode {
	cursor: pointer;
	font-size: 1em;
	border: none;
	box-shadow: none;
	background: #efefef;
	font-family: "courier new", courier, serif;
  }
}

.foobar-shortcode-message {
  display: none;
  font-style: italic;
  color: #2EA2CC !important;
}

tr.foobar-preview-loading {
  .row-title {
	position: relative;
	&:after {
	  content: '';
	  background: url(/wp-admin/images/spinner.gif) no-repeat;
	  background-size: 20px 20px;
	  display: inline-block;
	  opacity: 0.7;
	  filter: alpha(opacity=70);
	  width: 20px;
	  height: 20px;
	  border: none;
	  position: absolute;
	  top: 50%;
	  right: -24px;
	  transform: translateY(-50%);
	}
  }
}

#foobar_notification-settings_custom_color_background_gradient_angle-field .foofields-field-input input {
  width: 80px;
}

#foobar_notification-custom_css_custom_css-field .foofields-field-input > p {
  margin-top: 0;
}

@import "fields/index";
