// some variables specifying the sizing of the icon picker
$size: 30px;
$size-mobile: 32px;

$max-width: $size * 6 + 2px;
$max-width-mobile: 40px * 6 + 2px;
.foofields-type-icon-picker {
  .fip-container {

	display: inline-block;
	padding: 0;
	margin: 0;

	.fip-button {
	  display: inline-flex;
	  width: auto;
	  height: $size;
	  min-height: $size;
	  padding: 0;
	  box-sizing: border-box;
	  white-space: nowrap;
	  overflow: hidden;
	  font-size: 11px;

	  .fip-icon {
		display: inline-flex;
		width: $size;
		height: 100%;
		justify-content: center;
		align-content: center;
		align-items: center;
		background-color: #FFF;
		color: #1e8cbe;
	  }

	  .fip-text {
		display: inline-flex;
		width: auto;
		height: 100%;
		background: #f7f7f7;
		border-radius: 0 2px 2px 0;
		border-left: 1px solid #ccc;
		color: #555;
		line-height: 2.54545455;
		padding: 0 6px;
		text-align: center;
	  }
	}

	.fip-clear {
	  display: none;
	  margin-left: 2px;
	  padding: 0 8px;
	  line-height: 2.54545455;
	  min-height: 30px;
	}

	.fip-list {
	  display: none;
	  border: solid 1px #ddd;
	  background-color: #FFF;
	  margin: 6px 0 0 0;
	  padding: 0;
	  max-width: $max-width;

	  .fip-item {
		display: inline-flex;
		justify-content: center;
		align-content: center;
		align-items: center;
		width: $size;
		height: $size;
		color: #333;
		background-color: transparent;
		border: none;
		text-decoration: none;
		margin: 0;
		padding: 0;
		cursor: pointer;
		box-shadow: none;

		&.fip-selected {
		  color: #007cba;
		}
		&:hover {
		  background-color: #007cba;
		  color: #FFF;
		}
		&:focus {
		  box-shadow: 0 0 0 1px #007cba;
		  outline: 2px solid transparent;
		  outline-offset: 0;
		}
	  }
	}

	&.fip-visible {
	  .fip-clear {
		display: inline-block;
	  }
	  .fip-list {
		display: block;
	  }
	}

  }

  // force an element to be hidden
  .fip-hidden {
	display: none !important;
  }

  // hidden but accessible for screen readers
  .fip-sr {
	border: 0;
	clip: rect(1px,1px,1px,1px);
	-webkit-clip-path: inset(50%);
	clip-path: inset(50%);
	height: 1px;
	margin: -1px;
	overflow: hidden;
	padding: 0;
	position: absolute;
	width: 1px;
	word-wrap: normal!important;
  }
}

@media screen and (max-width: 782px){
  .foofields-type-icon-picker {
	.fip-container {
	  margin-top: 4px;

	  .fip-button {
		height: $size-mobile;
		min-height: $size-mobile;
		font-size: 14px;
		line-height: 2.14285714;

		.fip-icon {
		  width: 40px;
		}

		.fip-text {
		  padding: 0 14px;
		  line-height: inherit;
		}
	  }

	  .fip-clear {
		padding: 0 8px;
		line-height: 2.14285714;
		min-height: $size-mobile;
	  }

	  .fip-list {
		max-width: $max-width-mobile;

		.fip-item {
		  width: 40px;
		  height: 40px;
		}
	  }
	}
  }
}
