.foofields-type-conditions {
  .foofields-field-input {
	.foofields-conditions-help-message,
	.foofields-conditions-no-data-message {
	  display: flex;
	  margin: 0 0 10px 0;
	  width: 100%;
	  align-items: center;
	  min-height: 30px;
	  line-height: 1.4;
	}
	.foofields-conditions-no-data-message {
	  display: none;
	}

	.foofields-conditions-hr {
	  display: block;
	  width: 100%;
	  height: 1px;
	  background-color: #ccd0d4;
	  margin: 10px 0;
	}

	table {
	  display: table;
	  width: 100%;
	  margin: 0 0 10px 0;
	  padding: 0;
	  border-collapse: collapse;
	  border-spacing: 0;

	  > tr,
	  > thead > tr,
	  > tbody > tr {
		padding: 0;

		> td,
		> th {
		  padding: 0;

		  &:nth-of-type(1) {
			display: none;
		  }
		  > .foofields-field {
			margin-bottom: 0;
			.foofields-field-input {
			  padding: 0;
			}
		  }
		}
	  }

	  > tbody {
		> tr + tr {
		  > td[data-separator]:before {
			content: attr(data-separator);
			display: block;
			width: 100%;
			margin: 1em .25em;
		  }
		}
	  }

	  > tfoot {
		display: none;
	  }
	}
  }

  &.foofields-empty {
	.foofields-field-input {
	  .foofields-conditions-no-data-message {
		display: flex;
	  }

	  table,
	  .foofields-conditions-help-message {
		display: none;
	  }
	}
  }
}
