.foofields-type-conditions-group {
  .foofields-field-input {
	.foofields-conditions-group-empty-add,
	.foofields-conditions-group-empty-remove {
	  display: none;
	}

	.button + .button {
	  margin-left: .25em;
	}

	table {
	  display: table;
	  width: 100%;
	  margin: 0;
	  border-collapse: collapse;
	  border-spacing: 0;

	  &.widefat {
		> tr:not(:last-of-type) {
		  box-shadow: inset 0 -1px 0 rgba(0, 0, 0, 0.1);
		}
	  }

	  > tr,
	  > thead > tr,
	  > tbody > tr {

		&.ui-sortable-helper {
		  border: solid 1px rgba(0,0,0,0.1);
		  border-bottom: none;
		  background-color: #fff;
		}

		&.foofields-conditions-group-placeholder {
		  outline: 1px dashed #0073aa;
		  outline-offset: -10px;
		}

		> td,
		> th {
		  padding: 2px;

		  &:nth-of-type(1) {
			display: none;
		  }
		  &:nth-of-type(2) {
			padding-left: 0;
		  }
		  > .foofields-field {
			margin-bottom: 0;
			.foofields-field-input {
			  padding: 0;
			}
		  }
		}

	  }

	  > tbody {
		> tr {
		  > td,
		  > th {
			// the selectize column
			&:nth-of-type(4) {
			  width: 100%;
			}
		  }
		}
		> tr + tr {
		  > td:nth-of-type(2) {
			position: relative;
			&:before {
			  content: '&';
			  display: inline-flex;
			  position: absolute;
			  top: 50%;
			  left: 0;
			  width: 20px;
			  height: 20px;
			  justify-content: center;
			  align-content: center;
			  align-items: center;
			  transform: translateX(-100%) translateY(-50%);
			}
		  }
		}
	  }

	  > tfoot {
		display: none;
	  }
	}
  }

  &.foofields-empty {
	.foofields-field-input {
	  .foofields-conditions-group-empty-add,
	  .foofields-conditions-group-empty-remove {
		display: inline-block;
	  }

	  table {
		display: none;
	  }
	}
  }
}

.foofields-conditions-button {
  display: inline-flex;
  justify-content: center;
  align-content: center;
  align-items: center;
  transition: none;
  text-decoration: none;
  min-width: 30px;
  min-height: 30px;
  color: #0073aa;

  &:hover {
	color: #00a0d2;
  }

  // fix the forced size of 20x20 of dashicons which causes alignment problems
  .dashicons {
	display: inline-flex;
	justify-content: center;
	align-content: center;
	align-items: center;
  }
  .dashicons-trash:before {
	width: 19px;
  }
  .dashicons-plus:before {
	height: 17px;
  }
}
