.foofields-type-datetime {
  .foofields-time-selector {
	display: flex;
	flex-direction: row;
	flex-wrap: nowrap;

	.foofields-time-component {
	  display: inline-flex;
	  flex-direction: row;
	  flex-wrap: nowrap;
	  overflow: hidden;
	  width: 25%;
	  max-width: 6em;
	  box-shadow: 0 0 0 transparent;
	  border-radius: 4px;
	  border: 1px solid #7e8993;
	  background-color: #fff;
	  color: #32373c;
	  margin: 0 1px;

	  line-height: 2;
	  min-height: 30px;

	  &:focus,
	  &:focus-within {
		border-color: #007cba;
		box-shadow: 0 0 0 1px #007cba;
		outline: 2px solid transparent;
	  }

	  .foofields-time-input {
		display: inline-block;
		width: 100%;
		outline: none;
		box-shadow: none;
		border-radius: 0;
		border: none;
		background-color: #fff;
		color: #32373c;
		padding-right: 0;
		margin: 0;
	  }
	  .foofields-time-suffix {
		display: inline-block;
		padding: 2px;
		background-color: #f5f5f5;
		border-left: 1px solid #7e8993;
		text-align: center;
		min-width: 2em;
	  }
	}
  }

  &.foofields-error {
	.foofields-field-description {
	  color: #F00;
	}
  }
}
